.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}
.click__me {
  display: flex;
}
.move__contact {
  margin-left: 3%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  /* border-radius: 31% 69% 23% 77% / 66% 18% 82% 34%; */
                    
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  /* aspect-ratio: 1/1; */
  /* border-radius: 31% 69% 23% 77% / 66% 18% 82% 34%; */
                    
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: rotate(0);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  /* gap:1.5rem */
}

.about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
  animation: fadeIn 3s ease-in;
}
.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about__card h5 {
  font-size: 0.95rem;
}

.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}

.about__container a{
  animation: fadeInRight 3s ease-in-out;
}

@keyframes fadeInRight{
  from{
    opacity: 0;
    transform: translateX(300px);
  }
  to{
    opacity: 1;
  }
}

@keyframes fadeIn{
  from{
    opacity: 0;
    transform: translate3d(0,-20%,0);
  }
  to{
    opacity: 1;
    transform: translate3d(0,0,0);
  }
}

/*=============TABLET SCREEN ===========  */

@media only screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }

  /* #about {
    height: 100vh;
  } */
}
/*=============SMALL SCREEN ===========  */

@media only screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    /* margin-left: 18px; */
  }
  .about__content {
    text-align: center;
  }
  .about__content p {
    margin: 1.5rem 0;
  }
}
